.footer{
    padding-top: 15px;
    padding-bottom: 22px;
    @media only screen and (max-width: 1440px) {
        padding-top: 1.042vw;
        padding-bottom: 1.528vw;
    }
    @media only screen and (max-width: 650px) {
        padding-top: 6.944vw;
        padding-bottom: 6.944vw;
    }
    &__copyright{
        font-family: 'Circe';
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        color: #1F1F39;
        @media only screen and (max-width: 1440px) {
            font-size: 0.972vw;
            line-height: 1.319vw;
        }
        @media only screen and (max-width: 650px) {
            font-size: 3.333vw;
            line-height: 4.444vw;
            text-align: center;
            width: 100%;
            margin-bottom: 2.042vw;
        }
    }
    &__links{
        @media only screen and (max-width: 650px) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        & > *{
            margin-right: 15px;
            font-family: "Circe";
            font-weight: 400;
            font-size: 14px;
            line-height: 19px;
            color: #58A3C9;
            @media only screen and (max-width: 1440px) {
                margin-right: 1.042vw;
                font-size: 0.972vw;
                line-height: 1.319vw;
            }
            @media only screen and (max-width: 1000px) {
                font-size: 1.250vw;
                line-height: 1.528vw;
            }
            @media only screen and (max-width: 650px) {
                font-size: 3.333vw;
                line-height: 4.444vw;
                margin-right: 0;
            }
        }
    }
}