.buy{
    &__title{
        margin-bottom: 23px;
        font-family: 'Bebas Neue Cyrillic';
        font-size: 90px;
        line-height: 80px;
        text-align: center;
        color: #FF6A80;
        @media only screen and (max-width: 1440px) {
            margin-bottom: 1.597vw;
            font-size: 6.250vw;
            line-height: 5.556vw;
        }
        @media only screen and (max-width: 650px) {
            font-size: 14.722vw;
            line-height: 22.222vw;
            margin-bottom: 0;
        }
    }
    &__text{
        font-family: 'Circe';
        font-style: normal;
        font-weight: 400;
        font-size: 32px;
        line-height: 35px;
        text-align: center;
        color: #000000;
        margin-bottom: 23px;
        @media only screen and (max-width: 1440px) {
            font-size: 2.222vw;
            line-height: 2.431vw;
            margin-bottom: 1.597vw;
        }
        @media only screen and (max-width: 650px) {
            font-size: 5.833vw;
            line-height: 6.944vw;
            margin-bottom: 10vw;
        }
    }
    &__container{
        background: #F0F0F0;
        padding-top: 33px;
        padding-bottom: 29px;
        @media only screen and (max-width: 1440px) {
            padding-top: 2.292vw;
            padding-bottom: 2.014vw;
        }
        @media only screen and (max-width: 650px) {
            padding-top: 9.167vw;
            padding-bottom: 7.778vw;
        }
        .wrapper{
            display: flex;
            align-items: center;
            flex-direction: column;
        }
    }
    &__partners{
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 46px;
        @media only screen and (max-width: 1440px) {
            margin-bottom: 3.194vw;
            img{
                width: 11.667vw;
                height: 3.333vw;
            }
        }
        @media only screen and (max-width: 650px) {
            flex-wrap: wrap;
            img{
                width: 41.667vw;
                height: 11.667vw;
                margin-bottom: 2.778vw;
            }
        }
    }
    &__button{
        padding: 17px 38px;
        border: 1px solid #333333;
        border-radius: 4px;
        font-family: 'IBM Plex Sans';
        font-weight: 500;
        font-size: 18px;
        line-height: 25px;
        text-transform: uppercase;
        color: #333333;
        transition: 0.3s;
        cursor: pointer;
        @media only screen and (max-width: 1440px) {
            padding: 1.181vw 2.639vw;
            font-size: 1.250vw;
            line-height: 1.736vw;
        }
        @media only screen and (max-width: 650px) {
            padding: 4.722vw 10.556vw;
            font-size: 4.444vw;
            line-height: 6.111vw;
        }
        &:hover{
            background: #333;
            color: #fff;
        }
    }
}