.privilege{
    margin-bottom: 61px;
    @media only screen and (max-width: 1440px) {
        margin-bottom: 4.236vw;
    }
    @media only screen and (max-width: 650px) {
        margin-bottom: 10vw;
    }
    .wrapper{
        width: 1260px;
        @media only screen and (max-width: 1440px) {
            width: 87.500vw;
        }
        @media only screen and (max-width: 650px) {
            width: calc(100% - 3.889vw);
        }
    }
    &__title{
        font-family: 'Bebas Neue Cyrillic';
        font-style: normal;
        font-weight: 400;
        font-size: 90px;
        line-height: 74px;
        text-align: center;
        color: #FF6A80;
        margin-bottom: 60px;
        &.mobile{
            display: none;
        }
        @media only screen and (max-width: 1440px) {
            font-size: 6.250vw;
            line-height: 5.139vw;
            margin-bottom: 4.167vw;
        }
        @media only screen and (max-width: 650px) {
            &.mobile{
                display: block;
            }
            &.desc{
                display: none;
            }
            font-size: 14.722vw;
            line-height: 20.556vw;
            margin-bottom: 3.056vw;
        }
        span{
            color: #000;
        }
    }
    &__text{
        font-family: 'Circe';
        font-weight: 400;
        font-size: 32px;
        line-height: 35px;
        text-align: center;
        color: #58A3C9;
        @media only screen and (max-width: 1440px) {
            font-size: 2.222vw;
            line-height: 2.431vw;
        }
        @media only screen and (max-width: 650px) {
            font-size: 5vw;
            line-height: 5.556vw;
        }
    }
    &__container{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 18px;
        @media only screen and (max-width: 1440px) {
            margin-bottom: 1.250vw;
        }
        @media only screen and (max-width: 650px) {
            flex-direction: column;
        }
    }
    &__item{
        width: 614px;
        height: 408px;
        position: relative;
        @media only screen and (max-width: 1440px) {
            width: 42.639vw;
            height: 28.333vw;
        }
        @media only screen and (max-width: 650px) {
            width: 92.778vw;
            height: 61.389vw;
            margin-bottom: 7.222vw;
        }
        &-bg{
            width: 100%;
            height: 100%;
        }
        &-title{
            position: absolute;
            left: 16px;
            bottom: 14px;
            z-index: 3;
            @media only screen and (max-width: 1440px) {
                left: 1.111vw;
                bottom: 0.972vw;
                width: 22.500vw;
                height: 8.958vw;
            }
            @media only screen and (max-width: 650px) {
                left: 2.222vw;
                bottom: 1.667vw;
                width: 48.889vw;
                height: 19.444vw;
            }
        }
        &:nth-child(1){
            .privilege__prize img{
                &:nth-child(1){
                    top: 163px;
                    left: 112px;
                    z-index: 2;
                    @media only screen and (max-width: 1440px) {
                        top: 11.319vw;
                        left: 7.778vw;
                        width: 13.750vw;
                        height: 12.986vw;
                    }
                    @media only screen and (max-width: 650px) {
                        top: 24.444vw;
                        left: 16.944vw;
                        width: 27.222vw;
                        height: 25.556vw;
                    }
                }
                &:nth-child(2){
                    top: -15px;
                    left: 238px;
                    z-index: 1;
                    @media only screen and (max-width: 1440px) {
                        top: -1.042vw;
                        left: 16.528vw;
                        width: 22.847vw;
                        height: 27.847vw;
                    }
                    @media only screen and (max-width: 650px) {
                        top: -2.222vw;
                        left: 36.111vw;
                        width: 47.222vw;
                        height: 58.056vw;
                    }
                }
            }
        }
        &:nth-child(2){
            .privilege__prize img{
                top: 34px;
                left: 108px;
                @media only screen and (max-width: 1440px) {
                    top: 2.361vw;
                    left: 7.500vw;
                    width: 33.264vw;
                    height: 20.903vw;
                }
                @media only screen and (max-width: 650px) {
                    top: 5vw;
                    left: 16.667vw;
                    width: 70vw;
                    height: 43.056vw;
                }
            }
        }
        &:nth-child(3){
            .privilege__prize img{
                &:nth-child(1){
                    top: 86px;
                    left: 57px;
                    z-index: 2;
                    @media only screen and (max-width: 1440px) {
                        top: 5.972vw;
                        left: 3.958vw;
                        width: 24.792vw;
                        height: 14.375vw;
                    }
                    @media only screen and (max-width: 650px) {
                        top: 12.778vw;
                        left: 8.611vw;
                        width: 51.389vw;
                        height: 28.611vw;
                    }
                }
                &:nth-child(2){
                    top: -25px;
                    left: 227px;
                    z-index: 1;
                    @media only screen and (max-width: 1440px) {
                        top: -1.736vw;
                        left: 15.764vw;
                        width: 29.514vw;
                        height: 25.972vw;
                    }
                    @media only screen and (max-width: 650px) {
                        top: -3.611vw;
                        left: 34.167vw;
                        width: 61.667vw;
                        height: 53.889vw;
                    }
                }
                &:nth-child(3){
                    top: 139px;
                    left: 354px;
                    z-index: 3;
                    @media only screen and (max-width: 1440px) {
                        top: 9.653vw;
                        left: 24.583vw;
                        width: 12.917vw;
                        height: 16.944vw;
                    }
                    @media only screen and (max-width: 650px) {
                        top: 21.111vw;
                        left: 53.611vw;
                        width: 25.278vw;
                        height: 34.167vw;
                    }
                }
            }
        }
        &:nth-child(4){
            .privilege__prize img{
                &:nth-child(1){
                    top: 42px;
                    left: 85px;
                    z-index: 2;
                    @media only screen and (max-width: 1440px) {
                        top: 2.917vw;
                        left: 5.903vw;
                        width: 15.417vw;
                        height: 18.750vw;
                    }
                    @media only screen and (max-width: 650px) {
                        top: 6.389vw;
                        left: 12.778vw;
                        width: 30.833vw;
                        height: 38.056vw;
                    }
                }
                &:nth-child(2){
                    top: 90px;
                    left: 240px;
                    z-index: 3;
                    @media only screen and (max-width: 1440px) {
                        top: 6.250vw;
                        left: 16.667vw;
                        width: 15.764vw;
                        height: 17.847vw;
                    }
                    @media only screen and (max-width: 650px) {
                        top: 13.889vw;
                        left: 36.111vw;
                        width: 31.667vw;
                        height: 36.111vw;
                    }
                }
                &:nth-child(3){
                    top: 0px;
                    left: 331px;
                    z-index: 1;
                    @media only screen and (max-width: 1440px) {
                        top: 0vw;
                        left: 22.986vw;
                        width: 17.500vw;
                        height: 12.708vw;
                    }
                    @media only screen and (max-width: 650px) {
                        top: 0vw;
                        left: 50vw;
                        width: 34.167vw;
                        height: 25.556vw;
                    }
                }
                &:nth-child(4){
                    top: 188px;
                    left: 331px;
                    z-index: 4;
                    @media only screen and (max-width: 1440px) {
                        top: 13.056vw;
                        left: 22.986vw;
                        width: 21.667vw;
                        height: 12.083vw;
                    }
                    @media only screen and (max-width: 650px) {
                        top: 28.333vw;
                        left: 50vw;
                        width: 42.778vw;
                        height: 23.611vw;
                    }
                }
            }
        }

    }
    &__prize{
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 1;
        img{
            position: absolute;
            left: 0;
            top: 0;
            transition: 0.3s;
            &:hover{
                z-index: 6 !important;
                transform: scale(1.1);
            }
        }
    }
}