.popup{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(20,20,20,0);
    opacity: 0;
    z-index: 10;
    display: flex;
    transition: 0.3s;
    pointer-events: none;
    visibility: hidden;
    &.active{
        background: rgba(20,20,20,0.7);
        opacity: 1;
        pointer-events: auto;
        visibility: visible;
    }
    &__container{
        max-width: calc(100vw - 40px);
        width: 555px;
        padding: 30px;
        background: #FFFFFF;
        border: 3px solid #000000;
        border-radius: 8px;
        margin: auto;
        position: relative;
        max-height: calc(100vh - 100px);
        display: flex;
        flex-direction: column;
        @media only screen and (max-width: 650px) {
            width: 100vw;
            max-width: 100vw;
            max-height: 90vh;
            margin-top: auto;
            margin-bottom: 0;
            border-radius: 5.556vw 5.556vw 0px 0px;
            border: none;
            padding: 5.556vw 5.556vw 6.944vw 5.556vw;
        }
    }
    &__title{
        font-family: 'Akrobat';
        font-style: normal;
        font-weight: 900;
        font-size: 26px;
        line-height: 140%;
        text-align: center;
        text-transform: uppercase;
        color: #000000;
        flex-shrink: 0;
        margin-bottom: 20px;
        @media only screen and (max-width: 650px) {
            font-size: 4.722vw;
            line-height: 6.667vw;
            margin-bottom: 6.389vw;
        }
    }
    &__button{
        padding: 17px 0;
        text-align: center;
        width: 260px;
        flex-shrink: 0;
        font-family: 'IBM Plex Sans';
        font-weight: 500;
        font-size: 18px;
        line-height: 140%;
        text-transform: uppercase;
        color: #FFFFFF;
        background: #58A3C9;
        border-radius: 4px;
        align-self: center;
        margin-top: 20px;
        @media only screen and (max-width: 650px) {
            margin-top: 6.389vw;
            font-size: 3.889vw;
            padding: 2.222vw 0;
            width: 43.611vw;
        }
    }
    &__close{
        position: absolute;
        right: 20px;
        top: 20px;
        cursor: pointer;
        @media only screen and (max-width: 650px) {
            right: 3.056vw;
            top: 3.889vw;
        }
    }
    &__body{
        display: flex;
        flex-direction: column;
        overflow-y: auto;
        padding-right: 5px;
        h2{
            margin-bottom: 13px;
            font-family: 'Akrobat';
            font-weight: 900;
            font-size: 26px;
            line-height: 140%;
            color: #333333;
            @media only screen and (max-width: 650px) {
                font-size: 4.444vw;
                margin-bottom: 2.222vw;
            }
        }
        p{
            font-family: 'IBM Plex Sans';
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 140%;
            color: #333333;
            margin-bottom: 10px;
            @media only screen and (max-width: 650px) {
                font-size: 3.333vw;
                margin-bottom: 2.222vw;
            }
        }
        &::-webkit-scrollbar { /* 1 - скроллбар */
            width: 15px;
            height: 4px;
            background-color: #EFEFEF;
            @media only screen and (max-width: 650px) {
                width: 2.778vw;
            }
        }
        &::-webkit-scrollbar-thumb { /* 5 - ползунок */
            border-radius: 2px;
            background-color: #000;
        }
        & > *{
            flex-shrink: 0;
        }
    }
}
.popup-doc{
    .popup__container{
        width: 1038px;
    }
    .popup__body{
        max-height: 511px;
        padding-right: 80px;
        @media only screen and (max-width: 650px) {
            padding-right: 15px;
        }
    }
}
.mobile-menu{
    position: fixed;
    top: -100vh;
    left: 0;
    width: 100%;
    z-index: 4;
    background: #fff;
    padding: 2.778vw 5.556vw;
    display: none;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    border-radius: 0px 0px 5.556vw 5.556vw;
    transition: 0.3s;
    @media only screen and (max-width: 650px) {
        display: flex;
    }
    &.active{
        top: 17.5vw;
    }
    & > *{
        padding: 2.500vw 0;
        font-size: 5.556vw;
        line-height: 8.333vw;
        font-family: 'Circe';
        color: #000;
        border-bottom: 1px solid #B6B6B6;
        width: 100%;
        text-align: center;
        &:last-child{
            border-bottom: none;
        }
    }
}