.banner{
    background: linear-gradient(64.28deg, #58A3C9 9.99%, #7EBFD8 50.62%, #6AB1CD 85.73%);
    padding-bottom: 43px;
    padding-top: 70px;
    margin-bottom: 65px;
    overflow: hidden;
    @media only screen and (max-width: 1440px) {
        padding-bottom: 2.986vw;
        padding-top: 4.861vw;
        margin-bottom: 4.514vw;
    }
    @media only screen and (max-width: 650px) {
        padding-top: 7.222vw;
        padding-bottom: 0;
        margin-bottom: 12.778vw;
    }
    .wrapper{
        position: relative;
        display: flex;
        flex-direction: column;
        z-index: 1;
    }
    &__bg{
        position: absolute;
        left: 494px;
        bottom: -43px;
        z-index: -1;
        @media only screen and (max-width: 1440px) {
            left: 34.306vw;
            bottom: -2.986vw;
        }
        @media only screen and (max-width: 650px) {
            left: 1.944vw;
            bottom: 50.278vw;
        }
        img{
            display: block;
            @media only screen and (max-width: 1440px) {
                width: 76.319vw;
                height: 50.833vw;
            }
            @media only screen and (max-width: 650px) {
                width: 124.444vw;
                height: 82.778vw;
            }
        }
    }
}

.title{
    font-family: 'Bebas Neue';
    font-size: 140px;
    line-height: 115px;
    color: #000000;
    margin-bottom: 20px;
    @media only screen and (max-width: 1440px) {
        font-size: 9.722vw;
        line-height: 7.986vw;
        margin-bottom: 1.389vw;
    }
    @media only screen and (max-width: 650px) {
        font-size: 20.833vw;
        line-height: 17.222vw;
        margin-bottom: 0.556vw;
    }
    span{
        color: #fff;
    }
}
.text{
    font-family: 'Circe';
    font-weight: 400;
    font-size: 28px;
    line-height: 28px;
    color: #2F2F2F;
    max-width: 585px;
    margin-bottom: 20px;
    @media only screen and (max-width: 1440px) {
        font-size: 1.944vw;
        line-height: 1.944vw;
        max-width: 40.625vw;
        margin-bottom: 1.389vw;
    }
    @media only screen and (max-width: 650px) {
        font-size: 3.889vw;
        line-height: 4.167vw;
        max-width: 100%;
        margin-bottom: 0;
    }
}
.video{
    display: block;
    width: 468px;
    height: 235px;
    position: relative;
    margin-bottom: 27px;
    @media only screen and (max-width: 1440px) {
        width: 32.500vw;
        height: 16.319vw;
        margin-bottom: 1.875vw;
    }
    @media only screen and (max-width: 650px) {
        width: 100vw;
        height: 50.278vw;
        margin-left: -5.556vw;
        margin-bottom: 0;
        margin-top: 62.222vw;
    }
    &__icon{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: 0.3s;
        svg{
            @media only screen and (max-width: 1440px) {
                width: 7.778vw;
                height: 7.778vw;
            }
            @media only screen and (max-width: 650px) {
                width: 23.889vw;
                height: 23.889vw;
            }
        }
    }
    &::before{
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        content: '';
        background: rgba(0,0,0,0.5);
        transition: 0.3s;
    }
    &:hover{
        &::before{
            background: rgba(0,0,0,0.4);
        }
        .video__icon{
            transform: translate(-50%, -50%) scale(1.1);
        }
    }
    img{
        width: 100%;
        height: 100%;
    }
}
.button{
    padding: 19px 52px;
    background: #00ff00;
    align-self: baseline;
    font-family: 'Circe';
    font-weight: bold;
    font-size: 30px;
    line-height: 42px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    cursor: pointer;
    transition: 0.3s;
    @media only screen and (max-width: 1440px) {
        padding: 1.319vw 3.611vw;
        font-size: 2.083vw;
        line-height: 2.917vw;
    }
    @media only screen and (max-width: 650px) {
        display: none;
    }
    &:hover{
        background: darken(#00ff00, 5%);
    }
}