.steps{
    margin-bottom: 80px;
    @media only screen and (max-width: 1440px) {
        margin-bottom: 5.556vw;
    }
    @media only screen and (max-width: 650px) {
        margin-bottom: 15.556vw;
    }
    .wrapper{
        width: 1229px;
        display: flex;
        align-items: center;
        @media only screen and (max-width: 1440px) {
            width: 85.347vw;
        }
        @media only screen and (max-width: 650px) {
            flex-wrap: wrap;
            justify-content: space-between;
        }
    }
    &__item{
        position: relative;
        transition: 0.3s;
        @media only screen and (max-width: 650px) {
            margin-bottom: 1.667vw;
        }
        &:first-child{
            margin-right: 14px;
            @media only screen and (max-width: 1440px) {
                margin-right: 0.972vw;
            }
            z-index: 4;
            .steps__arrow{
                top: 86px;
                left: 222px;
                @media only screen and (max-width: 1440px) {
                    top: 5.972vw;
                    left: 15.417vw;
                    svg{
                        width: 10.903vw;
                        height: 4.653vw;
                    }
                }
                @media only screen and (max-width: 650px) {
                    top: 11.389vw;
                    left: 29.444vw;
                    svg{
                        width: 20.556vw;
                        height: 8.611vw;
                    }
                }
            }
            @media only screen and (max-width: 1440px) {
                img{
                    width: 20.278vw;
                    height: 20.417vw;
                }
            }
            @media only screen and (max-width: 650px) {
                img{
                    width: 38.889vw;
                    height: 38.889vw;
                }
            }
        }
        &:nth-child(2){
            z-index: 3;
            .steps__arrow{
                top: 115px;
                left: 262px;
                svg{
                    &:last-child{
                        display: none;
                    }
                }
                @media only screen and (max-width: 1440px) {
                    top: 7.986vw;
                    left: 18.194vw;
                    svg{
                        width: 10.486vw;
                        height: 4.028vw;
                    }
                }
                @media only screen and (max-width: 650px) {
                    top: 34.444vw;
                    left: -9.167vw;
                    svg{
                        &:first-child{
                            display: none;
                        }
                        &:last-child{
                            display: block;
                        }
                        width: 16.111vw;
                        height: 14.722vw;
                    }
                }
            }
            @media only screen and (max-width: 1440px) {
                img{
                    width: 21.667vw;
                    height: 21.667vw;
                }
            }
            @media only screen and (max-width: 650px) {
                img{
                    width: 41.389vw;
                    height: 41.389vw;
                }
            }
        }
        &:nth-child(3){
            z-index: 2;
            .steps__arrow{
                top: 74px;
                left: 242px;
                svg{
                    &:last-child{
                        display: none;
                    }
                }
                @media only screen and (max-width: 1440px) {
                    top: 5.139vw;
                    left: 16.806vw;
                    svg{
                        width: 10.833vw;
                        height: 3.958vw;
                    }
                }
                @media only screen and (max-width: 650px) {
                    top: 22.778vw;
                    left: 30.556vw;
                    svg{
                        width: 20.278vw;
                        height: 9.444vw;
                        &:first-child{
                            display: none;
                        }
                        &:last-child{
                            display: block;
                        }
                    }
                }
            }
            @media only screen and (max-width: 1440px) {
                img{
                    width: 21.111vw;
                    height: 21.250vw;
                }
            }
            @media only screen and (max-width: 650px) {
                img{
                    width: 40.278vw;
                    height: 40.278vw;
                }
            }
        }
        &:last-child{
            margin-left: 14px;
            @media only screen and (max-width: 1440px) {
                margin-right: 0.972vw;
                img{
                    width: 20.208vw;
                    height: 20.347vw;
                }
            }
            @media only screen and (max-width: 650px) {
                img{
                    width: 38.889vw;
                    height: 38.889vw;
                }
            }
        }
        &.hidden{
            opacity: 0;
        }
    }
    &__arrow{
        position: absolute;
        transition: 0.3s;
        &.hidden{
            opacity: 0;
        }
    }
}