.header {
    padding-top: 14px;
    padding-bottom: 21px;
    background: #fff;
    height: 98px;
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 5;
    transition: 0.3s;
    @media only screen and (max-width: 1440px) {
        height: 6.806vw;
        padding-top: 0.972vw;
        padding-bottom: 1.458vw;
    }
    @media only screen and (max-width: 650px) {
        height: 17.500vw;
        padding-top: 2.778vw;
        padding-bottom: 4.444vw;
    }
    .wrapper{
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media only screen and (max-width: 650px) {
            justify-content: flex-end;
            width: calc(100% - 11.111vw);
        }
    }
    &__logo{
        flex-shrink: 0;
        @media only screen and (max-width: 1440px) {
            img{
                width: 5.486vw;
                height: 4.028vw;
            }
        }
        @media only screen and (max-width: 650px) {
            margin-right: auto;
            img{
                width: 13.889vw;
                height: 10.278vw;
            }
        }
    }
    &__menu{
        display: none;
        @media only screen and (max-width: 650px) {
            margin-left: 8.333vw;
            display: block;
            svg{
                width: 8.333vw;
                height: 8.333vw;
            }
        }
    }
    &__button{
        display: none;
        padding: 12px 34px;
        background: #00ff00;
        font-family: 'Circe';
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 29px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        transition: 0.3s;
        cursor: pointer;
        @media only screen and (max-width: 1440px) {
            padding: 0.833vw 2.361vw;
            font-size: 1.389vw;
            line-height: 2.014vw;
        }
        &:hover{
            background: darken(#00ff00, 5%);
        }
        @media only screen and (max-width: 650px) {
            display: block;
            padding: 2.222vw 3.056vw;
            font-size: 3.611vw;
            line-height: 5.278vw;
        }
    }
    &__links{
        display: flex;
        justify-content: center;
        flex-grow: 1;
        padding-left: 30px;
        font-family: 'Circe';
        font-weight: 300;
        @media only screen and (max-width: 1440px) {
            padding-left: 2.083vw;
        }
        & > *{
            margin-right: 45px;
            font-size: 20px;
            line-height: 30px;
            text-transform: uppercase;
            color: #000000;
            @media only screen and (max-width: 1440px) {
                margin-right: 3.125vw;
                font-size: 1.389vw;
                line-height: 2.083vw;
            }
            &:last-child{
                margin-right: 0;
            }
            &:hover{
                font-weight: 400;
            }
        }
        @media only screen and (max-width: 650px) {
            display: none;
        }
    }
    &.scrolled {
        box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.25);
        .header__button{
            display: block;
        }
        .header__links{
            display: none;
        }
    }
}