html,
body {
    margin: 0;
    padding: 0;
}

html {
    height: 100%;
    padding-top: 98px;
    @media only screen and (max-width: 1440px) {
        padding-top: 6.806vw;
    }
    @media only screen and (max-width: 650px) {
        padding-top: 17.500vw;
    }
}

body {
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    min-height: 100vh;
    position: relative;
    // font-family: 'Noto Sans', sans-serif;
    -webkit-overflow-scrolling: touch;
    & > * {
        flex-shrink: 0;
    }
}

.no-scroll {
    overflow: hidden;
}

* {
    outline: none !important;
    -webkit-tap-highlight-color: transparent;
    box-sizing: border-box;
}

//Убрать стили айфона у инпутов
input[type='text'],
input[type='email'],
input[type='password'] {
    // background: transparent;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
}

//Убирает крестик у инпутов ie
input::-ms-clear {
    display: none;
}

//Убирает глазик на type="password" в ie
input::-ms-reveal {
    display: none;
}

input[type='number'] {
    -moz-appearance:textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

a {
    text-decoration: none;
    cursor: pointer;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

.wrapper {
   width: 1200px;
   margin: 0 auto;
   @media only screen and (max-width: 1440px) {
        width: 83.333vw;
   }
   @media only screen and (max-width: 650px) {
        width: calc(100% - 11.111vw);
   }
}

input{
    padding: 14px 19px;
    font-family: 'IBM Plex Sans';
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: #000;
    border: 1px solid #333333;
    border-radius: 4px;
    margin-bottom: 10px;
    @media only screen and (max-width: 650px) {
        padding: 3.889vw 5.278vw;
        font-size: 5vw;
    }
    &:last-child{
        margin-bottom: 0;
    }
    &::placeholder{
        color: #333333;
    }
}
textarea{
    resize: none;
    height: 217px;
    padding: 14px 19px;
    font-family: 'IBM Plex Sans';
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: #000;
    border: 1px solid #333333;
    border-radius: 4px;
    margin-bottom: 10px;
    &:last-child{
        margin-bottom: 0;
    }
    &::placeholder{
        color: #333333;
    }
}
.checkbox{
    position: relative;
    margin-bottom: 10px;
    display: block;
    &:last-child{
        margin-bottom: 0;
    }
    input{
        display: none;
    }
    span{
        font-family: "IBM Plex Sans";
        font-size: 14px;
        padding-left: 36px;
        display: block;
        @media only screen and (max-width: 650px) {
            font-size: 3.889vw;
        }
        a{
            color: #58A3C9;
        }
    }
    @media only screen and (max-width: 650px) {
        margin-bottom: 2.778vw;
    }
}
.checkbox span::before {
    left: 0;
    top: -3px;
    width: 26px;
    height: 26px;
    border: 1px solid #b6b6b6;
}
.checkbox input:checked+span::before {
    border-color: #58A3C9;
}
.checkbox span::after, .checkbox span::before {
    content: "";
    position: absolute;
    -webkit-transition: .3s;
    transition: .3s;
}
.checkbox input:checked+span::after {
    -webkit-transform: scale(1);
    transform: scale(1);
}
.checkbox span::after {
    left: 7px;
    top: 4px;
    width: 14px;
    height: 14px;
    background: #58A3C9;
    -webkit-transform: scale(0);
    transform: scale(0);
}
